import { useState } from "react"
import styled from "styled-components";
import { Block } from "../Block";
import { Modal } from "./Modal";
import { Button } from "../Button";
import { InfoModal } from "./InfoModal";
import { Title } from "../Title";

const Content = styled(Block)`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: ${({$isOpen}) => $isOpen ? 0 : 1};
    transition: opacity 150ms;
`;

const TitleStyled = styled(Title)`
    margin-bottom: var(--spacing_x3);
`;

export const RulesModal = ({ onClose, isOpen }) => {
    const [openedPart, setOpenedPart] = useState();

    return (
        <Modal isDarken isOpen={isOpen}>
            <Content hasCloseIcon onClose={onClose} $isOpen={openedPart !== undefined}>
                <TitleStyled></TitleStyled>
                <Button onClick={() => setOpenedPart(0)}> </Button>
                <Button onClick={() => setOpenedPart(1)}></Button>
                <Button onClick={() => setOpenedPart(2)}></Button>
                <Button onClick={() => setOpenedPart(3)}> </Button>
                <Button onClick={() => setOpenedPart(4)}></Button>
            </Content>
            <InfoModal key={`info_${openedPart}`} isOpen={openedPart !== undefined} initialPart={openedPart} onClose={() => setOpenedPart()}/>
        </Modal>
    )
}